<template><section>
<h2>axios请求封装</h2>
<pre v-pre=""><code class="hljs language-javascript">  <span class="hljs-keyword">import</span> { Request } <span class="hljs-keyword">from</span> <span class="hljs-string">'dby-base'</span>
  Vue.use(Request)
  <span class="hljs-comment">// 访问 this.$request</span>
</code></pre>
<h4>login登录请求</h4>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-button color=\&quot;danger\&quot; @click=\&quot;onClickRequest\&quot;&gt;点击发起登录请求&lt;/base-button&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  methods: {\n    onClickRequest () {\n      // 登录接口需要首次进入\n      this.$request.login( // 不管请求多少次只会发起一次请求 等请求回来后就会自动回调\n        { \n          data:{}, \n          succ(res){\n            let token = res.token\n          }, fail(res){\n\n          } \n        }\n      )\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <base-button color="danger" @click="onClickRequest">点击发起登录请求</base-button>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onClickRequest"</span>&gt;</span>点击发起登录请求<span class="hljs-tag">&lt;/<span class="hljs-name">base-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    onClickRequest () {
      <span class="hljs-comment">// 登录接口需要首次进入</span>
      <span class="hljs-keyword">this</span>.$request.login( <span class="hljs-comment">// 不管请求多少次只会发起一次请求 等请求回来后就会自动回调</span>
        { 
          <span class="hljs-attr">data</span>:{}, 
          succ(res){
            <span class="hljs-keyword">let</span> token = res.token
          }, fail(res){

          } 
        }
      )
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h4>getToken获得token</h4>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-button color=\&quot;danger\&quot; @click=\&quot;onClickRequestToken\&quot;&gt;点击发起token请求&lt;/base-button&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  methods: {\n    onClickRequestToken() {\n      // 获取token \n      // 按照规则是：\n      // 1.当前url有token就用url的 \n      // 2.当前缓存是否有token有用缓存的\n      // 3.当前缓存url是否有code 有去登录\n      // 4.当前本地有没有token 有去检查token 然后再返回\n      this.$request.getToken( \n        { \n          data:{}, \n          succ(res){\n            let token = res.token\n          }, fail(res){\n\n          } \n        }\n      )\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <base-button color="danger" @click="onClickRequestToken">点击发起token请求</base-button>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onClickRequestToken"</span>&gt;</span>点击发起token请求<span class="hljs-tag">&lt;/<span class="hljs-name">base-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    onClickRequestToken() {
      <span class="hljs-comment">// 获取token </span>
      <span class="hljs-comment">// 按照规则是：</span>
      <span class="hljs-comment">// 1.当前url有token就用url的 </span>
      <span class="hljs-comment">// 2.当前缓存是否有token有用缓存的</span>
      <span class="hljs-comment">// 3.当前缓存url是否有code 有去登录</span>
      <span class="hljs-comment">// 4.当前本地有没有token 有去检查token 然后再返回</span>
      <span class="hljs-keyword">this</span>.$request.getToken( 
        { 
          <span class="hljs-attr">data</span>:{}, 
          succ(res){
            <span class="hljs-keyword">let</span> token = res.token
          }, fail(res){

          } 
        }
      )
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h4>checkVisitor检查token</h4>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-button color=\&quot;danger\&quot; @click=\&quot;onClickCheckVisitor\&quot;&gt;检测token请求&lt;/base-button&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  methods: {\n    onClickCheckVisitor() {\n      this.$request.checkVisitor(\n        { \n          token: 'wangjiahui',\n          data:{}, \n          succ(res){\n            let token = res.token\n          }, fail(res){\n\n          } \n        }\n      )\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <base-button color="danger" @click="onClickCheckVisitor">检测token请求</base-button>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onClickCheckVisitor"</span>&gt;</span>检测token请求<span class="hljs-tag">&lt;/<span class="hljs-name">base-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    onClickCheckVisitor() {
      <span class="hljs-keyword">this</span>.$request.checkVisitor(
        { 
          <span class="hljs-attr">token</span>: <span class="hljs-string">'wangjiahui'</span>,
          <span class="hljs-attr">data</span>:{}, 
          succ(res){
            <span class="hljs-keyword">let</span> token = res.token
          }, fail(res){

          } 
        }
      )
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h4>httpRequest发起请求</h4>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-button color=\&quot;danger\&quot; @click=\&quot;onClickHttpRequest\&quot;&gt;发起请求&lt;/base-button&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  methods: {\n    /**\n     * 用于http请求\n     * @param {*} { path, header, data, method, succ, fail, baseURL, clog }\n     * @param path当前地址\n     * @param header请求头\n     * @param data 参数\n     * @param method 请求方式\n     * @param succ 成功\n     * @param fail 失败\n     * @param baseURL 请求环境\n     * @param noToken 需不需要携带token默认需要\n     */\n    onClickHttpRequest() {\n      this.$request.httpRequest(\n        {\n          path: '/dome',\n          header: {},\n          data: {},\n          method: 'POST',\n          succ:(res)=&gt; {\n            this.$toast('请求成功')\n          },\n          fail:(res)=&gt; {\n            this.$toast('请求失败')\n          },\n          baseURL:this.$env.apiH5,\n          noToken: true\n        }\n      )\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <base-button color="danger" @click="onClickHttpRequest">发起请求</base-button>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onClickHttpRequest"</span>&gt;</span>发起请求<span class="hljs-tag">&lt;/<span class="hljs-name">base-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    <span class="hljs-comment">/**
     * 用于http请求
     * @param {*} { path, header, data, method, succ, fail, baseURL, clog }
     * @param path当前地址
     * @param header请求头
     * @param data 参数
     * @param method 请求方式
     * @param succ 成功
     * @param fail 失败
     * @param baseURL 请求环境
     * @param noToken 需不需要携带token默认需要
     */</span>
    onClickHttpRequest() {
      <span class="hljs-keyword">this</span>.$request.httpRequest(
        {
          <span class="hljs-attr">path</span>: <span class="hljs-string">'/dome'</span>,
          <span class="hljs-attr">header</span>: {},
          <span class="hljs-attr">data</span>: {},
          <span class="hljs-attr">method</span>: <span class="hljs-string">'POST'</span>,
          <span class="hljs-attr">succ</span>:<span class="hljs-function">(<span class="hljs-params">res</span>)=&gt;</span> {
            <span class="hljs-keyword">this</span>.$toast(<span class="hljs-string">'请求成功'</span>)
          },
          <span class="hljs-attr">fail</span>:<span class="hljs-function">(<span class="hljs-params">res</span>)=&gt;</span> {
            <span class="hljs-keyword">this</span>.$toast(<span class="hljs-string">'请求失败'</span>)
          },
          <span class="hljs-attr">baseURL</span>:<span class="hljs-keyword">this</span>.$env.apiH5,
          <span class="hljs-attr">noToken</span>: <span class="hljs-literal">true</span>
        }
      )
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>

<script>
export default {
  methods: {
    onClickRequest () {
      this.$request.login(
        { 
          data:{}, 
          succ(res){
            let token = res.token
          }, fail(res){

          } 
        }
      )
    },
    onClickRequestToken() {
      this.$request.getToken(
        { 
          data:{}, 
          succ(res){
            let token = res.token
          }, fail(res){

          } 
        }
      )
    },
    onClickCheckVisitor() {
      this.$request.checkVisitor(
        { 
          token: 'wangjiahui',
          data:{}, 
          succ(res){
            let token = res.token
          }, fail(res){

          } 
        }
      )
    },
    onClickHttpRequest() {
      this.$request.httpRequest(
        {
          path: '/dome',
          header: {},
          data: {},
          method: 'POST',
          succ:(res)=> {
            this.$toast('请求成功')
          },
          fail:(res)=> {
            this.$toast('请求失败')
          },
          baseURL:this.$env.apiH5,
          noToken: true
        }
      )
    }
  }
}
</script>


